@-webkit-keyframes fa-spin {

0% {
	-webkit-transform: rotate(0deg);
	transform: rotate(0deg);
}

100% {
	-webkit-transform: rotate(359deg);
	transform: rotate(359deg);
}

}

@keyframes fa-spin {

0% {
	-webkit-transform: rotate(0deg);
	transform: rotate(0deg);
}

100% {
	-webkit-transform: rotate(359deg);
	transform: rotate(359deg);
}

}

@keyframes image {

0% {
	transform: translatey(-10px);
}

100% {
	transform: translatey(10px);
}

}

@keyframes ripple {

0% {
	transform: translate(-50%, -50%) scale(0);
}

100% {
	transform: translate(-50%, -50%) scale(1);
}

}

@keyframes text {

0% {
	opacity: 0;
}

50% {
	opacity: 0.5;
}

100% {
	opacity: 1;
}

}

@keyframes slide-right {

from {
	width: 0%;
	opacity: 0;
}

to {
	width: 100%;
	opacity: 1;
}

}

@keyframes slide-right-text {

from {
	transform: translateX(-100%);
	opacity: 0;
}

to {
	transform: translateX(0%);
	opacity: 1;
}

}

@keyframes movement-of-ball {

0% {
	opacity: 1;
}

25% {
	transform: translateX(25vw) translateY(-15vh);
	opacity: 1;
}

50% {
	transform: translateX(50vw) translateY(-22vh);
	opacity: 1;
}

75% {
	transform: translateX();
	opacity: 1;
}

100% {
	transform: translateX(calc(100vw - 150px)) translateY(calc(150px - 92vh));
	opacity: 1;
}

}

@keyframes slide-up {

0% {
	transform: translateY(100%);
	opacity: 1;
}

100% {
	transform: translateY(0%);
	opacity: 1;
}

}

